body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Brandon", "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-image:url("./images/bg_color.svg"); */
  /* background-repeat: no-repeat; */
  /* background-size: cover; */
  /* background-position: center; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: 'Brandon';
  font-style: normal;
  font-weight: normal;
  src:
    local('Brandon reg'),
    local('Brandon-reg'),
    url(/static/media/Brandon_reg.1f55925e.otf) format('opentype');
}

@font-face {
  font-family: 'Brandon Bold';
  font-style: bold;
  font-weight: bold;
  src:
    local('Brandon blk'),
    local('Brandon-blk'),
    url(/static/media/Brandon_blk.c10f15f1.otf) format('opentype');
}

h1 {
  font-family: 'Brandon Bold';
}

h2, h3, h4, h5, p {
  font-family: 'Brandon';
}

.d-flex {
  display: flex;
  width: 100%;
}

.fw{
  flex-wrap: wrap;
}

.jcc {
  justify-content: center;
}

.aic {
  align-items: center;
}
/* Navbar */
.navbar {
  display: flex;
  flex-direction: column;
  height: 75px;
}
.navbar .pleca {
  height: 5px;
  width: 100%;
  position: fixed;
  top: 70px;
  z-index: 100;
}

.nav {
  width: 100%;
  height: 70px;
  color: black;
  background-color: white;
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: space-between;
  position: fixed;
  top: 0;
  z-index: 100;
}

.nav div {
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
}

.nav .logo {
  height: 70px;
  margin-left: 0px;
}

.nav span {
  margin: 0 20px;
  cursor: pointer;
  font-size: 1rem;
  transition: all .5s ease;
}

.btn-donar {
  color: white;
  background-color: #90b441;
  width: 80px;
  height: 35px;
  margin-right: 30px;
}


.btn-donar span {
  color: white;
  margin: 0 20px;
  cursor: pointer;
  font-size: 1rem;
  transition: all .5s ease;
}

.nav span:hover {
  font-weight: bold;
  color: black;
}

.btn-donar span:hover {
  font-weight: bold;
  color: white;
}

.nav a {
  text-decoration: none;
  color: black;
  margin: 0 20px;
  font-size: 1.5em;
}

.tn-donar a {
  text-decoration: none;
  color: white;
  margin: 0 20px;
  font-size: 1.5em;
}
/* Landing */
/* h1 {
  margin-top: 100px !important;
} */
/* Presentation */
.presentation {
  display: flex;
  height: 650px;
  background-image: url(/static/media/bk_presentation.82eb0dfb.png);
  background-color: white;
  background-position: right;
  background-repeat: no-repeat;
  background-size: cover;
}

.presentation-data {
  width: 40%;
  margin: 100px 20px 0 30px;
}

.presentation-data h3 {
  width: 87%;
  font-size: 36px;
  line-height: 2.5rem;
}

.presentation-data h4 {
  width: 92%;
  font-size: 36px;
  line-height: 2.5rem;
}

.presentation-data p {
  width: 100%;
  font-size: 22px;
  margin: 40px 0;
  line-height: 1.5rem;
}

.play-container {
  display: flex;
  align-content: center;
  align-items: center;
  margin: 30px 0;
}

.play-icon {
  width: 40px;
  height: 40px;
  margin-right: 5px;
}

.play-container span {
  font-size: 22px;
}

.btn-container {
  display: flex;
}

.btn-presentation {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #e0e0e0;
  width: 150px;
  height: 40px;
  margin: 20px 30px;
}

.btn-presentation span {
  color: black;
  cursor: pointer;
  font-size: 1rem;
  transition: all .5s ease;
}

.btn-presentation span:hover {
  font-weight: bold;
  color: black;
}
/* Projects */

.projects {
  height: 640px;
  background-color: #f5f5f5;
}

.projects h2 {
  padding-top: 60px;
  margin: 0 30px;
  font-size: 30px;
  font-weight: bold;
  font-family: 'Brandon Bold';
}

.projects p {
  width: 70%;
  font-size: 22px;
  margin: 30px;
  line-height: 1.5rem;
}

.obj-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.obj-container img {
  width: 195px;
}
/* Benefits */
.benefits {
  height: 640px;
  display: flex;
  justify-content: flex-end;
  background-image: url(/static/media/bk_benefits.595efcf9.png);
  background-color: white;
  background-position: left;
  background-repeat: no-repeat;
  background-size: cover;
}

.benefits-data {
  width: 45%;
  height: 640px;
  margin: 30px;
}

.benefits-data h2 {
  font-size: 30px;
  font-weight: bold;
  font-family: 'Brandon Bold';
}

.benefits-data h4 {
  margin: 40px;
  font-size: 30px;
  font-weight: bold;
  font-family: 'Brandon Bold';
}

.benefits-data p {
  width: 100%;
  font-size: 26px;
  margin: 40px 0;
  line-height: 1.5rem;
}

.benefits-data li {
  width: 100%;
  font-size: 18px;
  line-height: 2.5rem;
}
/* How it works */
.how {
  height: 680px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  background-image: url(/static/media/bk_how.ce7c4621.png);
  background-color: #f5f5f5;
  background-position: left;
  background-repeat: no-repeat;
  background-size: cover;
}

.how h3 {
  font-size: 50px;
  margin: 30px;
}

.how-data {
  width: 40%;
  margin: 30px;
}

.how-data h2 {
  font-size: 30px;
  font-weight: bold;
  font-family: 'Brandon Bold';
}

.how-data h4 {
  font-size: 30px;
  font-family: 'Brandon';
}

.how-data li {
  width: 85%;
  font-size: 18px;
  margin: 20px 0;
}

.how-register {
  display: flex;
  flex-direction: column;
  align-items: center;
}
/* Footer */
.footer {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.footer .pleca {
  height: 5px;
  width: 100%;
}

.footer-data-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 30px 30px 15px 30px;
}

.footer-data-container .logo-footer {
  height: 100px;
}

.footer-data-container h5 {
  font-size: 20px;
}

.logos-footer {
  display: flex;
  justify-content: flex-end;
  text-align: right;
}

.logos-footer span {
  margin-left: 20px;
  cursor: pointer;
}

.contact-footer {
  display: flex;
  justify-content: flex-end;
  text-align: right;
  margin: 20px 0;
}

.contact-footer span {
  margin-left: 20px;
}

.contact-footer .link {
  cursor: pointer;
}

.social {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f5f5f5;
  padding: 10px 30px;
}

.social-icons {
  display: flex;
  align-items: center;
}

.social-icons img {
  height: 30px;
  margin-left: 5px;
  cursor: pointer;
}
.form {
  margin: 0 130px;
}

.ant-form-item {
  padding-bottom: 3px;
  margin-bottom: 3px;
}

.auth {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
}

.auth-container {
  width: 100%;
  height: 100%;
}

.auth-form h2 {
  margin-top: 100px;
}

/* user */
.user-signup-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.user-radio-container {
  margin: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-content: center;
  align-items: center;
  text-align: left;
}

.radio-files-container {
  width: 100%;
  height: 100%;
  margin: 10px 0;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  text-align: center;
  color: white;
}

.radio-container {
  width: 45%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-content: space-between;
  align-items: center;
  text-align: left;
  color: white;
}

.files-container {
  width: 45%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: space-around;
  align-items: center;
  text-align: center;
  color: white;
}

.files-container input, button {
  width: 400px;
  margin: 0 10px;
}

.user-container, .info-container {
  width: 45%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: space-around;
  align-items: center;
  text-align: center;
  color: white;
}

.user-container input, button {
  width: 400px;
}

.login-form {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
}

.login-form input {
  width: 400px;
}

.login-form button {
  width: 150px;
}

/* Organization */
.org-basic-container {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-content: center;
  align-items: center;
  text-align: center;
}

.org-basic-container input {
  width: 300px;
}

.org-basic-container button {
  width: 300px;
}

.org-resources-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: center;
  align-items: center;
  text-align: left;
}

.org-data-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-content: center;
  align-items: center;
  text-align: left;
}

.org-data-container input {
  width: 300px;
  
}

.org-data-container button {
  width: 300px;
}

/* Visit */

.visit-container {
  margin-top: 100px;
  display: flex;
  justify-content: center;
  align-content: center;
}

.visit-container input {
  width: 400px;
}

.isotipo {
  position: fixed;
  top: 70px;
  right: 50px;
  width: 100px;
}

/* Formación */
.schedule-container {
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.schedule-container span {
  margin-right: 5px;
  margin-left: 5px;
}

/* Login */

.login-isotipo-form {
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.login-isotipo {
  margin: 10px;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.login-isotipo img {
  display: block;
}

/* SignUp Donatario */
.donatario {
  width: 850px;
  text-align: center;
  margin: 0 auto;
}
.donatario-form h3 {
  margin-top: 20px;
}
.donatario-info {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.donatario-info input {
  width: 400px;
  margin-right: 10px;
}
.donatario-info div {
  width: 400px;
  margin-right: 10px;
}
/* SignUp Organización */
.organizacion {
  width: 850px;
  text-align: center;
  margin: 0 auto;
}
.organizacion input {
  width: 850px;
}
.organizacion-form h3 {
  margin: 30px 0;
}
.organizacion-info {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.organizacion-info input {
  width: 400px;
}
.organizacion-info div {
  width: 400px;
}
.organizacion-radio {
  display: flex;
  justify-content: space-between;
}
.organizacion-radio-element {
  text-align: left;
}
/* Objetivos */

.objetivos {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.obj-header {
  max-width: 800px;
  text-align: center;
  margin: 0 auto;
}

.obj-container {
  margin: 10px;
  display: flex;
  flex-wrap: wrap;
  flex-grow: 6;
  justify-content: center;
  align-content: center;
}

.obj-container img {
  margin: 5px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

